import React, { Component } from 'react';

import { BrowserRouter as Router } from 'react-router-dom';
import { Switch, Route } from 'react-router-dom';

class App extends Component {
  render () {
    return (
      <div>
        <h1>Hello React</h1>
        <Router>
          <div>
          <Switch>
            <Route exact path="/" render={() => {
              return (
                <div>
                  <h2>Home page</h2>
                </div> 
              )
            }} />
            <Route path="/page2" render={() => {
              return (
                <div>
                  <h2>Page 2</h2>
                </div> 
              )
            }} />
            <Route render={() => {
              return (
                <div>
                  <h2>404 Not Found</h2>
                </div> 
              )
            }} />
          </Switch>
          </div>
        </Router>
      </div>
    )
  }
}

export default App